.articles {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: dense;
  grid-gap: 50px;
  width: 100%;

  @include mq($from: tablet) {
    grid-template-columns: 1fr 1fr;
  }

  @include mq($from: desktop) {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.article {
  .articles & {
    position: relative;
    max-width: 640px;
    margin: 0 auto;

    &.article--banner {
      grid-column: 1 / -1;
      max-width: none;
      width: 100%;

      // @include mq($from: tablet, $until: desktop) {
      //   &:first-of-type {
      //     grid-row: 2 / span 1;
      //   }
      // }
    }

    &:before {
      content: "";
      position: absolute;
      top: -10px;
      right: -25px;
      bottom: -10px;
      left: -25px;
      background-image: radial-gradient(circle closest-side at center, $color-omega, $color-gamma);
      opacity: .1;
      z-index: -1;
    }

    &:hover:before,
    &:focus:before,
    &:active:before {
      opacity: 1;
    }
  }

  li,
  p {
    a {
      color: $color-beta;
    }
  }

  blockquote {
    border-left: 1px solid $color-alpha;
    margin: 2em 0;
    padding: 5px 20px 10px;
    position: relative;

    h3 {
      text-transform: none;

      &:after {
        content: none;
      }
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: calc((100vw - 100%) / -2);
      bottom: 0;
      left: calc((100vw - 100%) / -2);
      background-color: $color-gamma;
      z-index: -1;
    }

    @include mq($from: tablet) {
      padding: 20px 60px 1.414rem;
    }

    .main--sidebar & {
      @include mq($from: tablet) {
        margin-top: 0;
        margin-bottom: 3px;
        padding-top: 60px;
        padding-bottom: 60px;
      }
    }

    .main--sidebar &:before {
      @include mq($from: tablet) {
        right: 0;
      }
    }
  }
}

.table-wrapper {
  width: 100%;
  overflow-y: auto;

  table {
    min-width: 100%;
    border: 1px solid lighten($color-alpha, 30%);
    border-collapse: collapse;
    font-size: ms(-1);

    th,
    td {
      text-align: left;
      border: 1px solid lighten($color-alpha, 30%);
      padding: .5rem;
      min-width: 80px;
    }

    th {
      background-color: lighten($color-alpha, 40%);
    }

    td {
      background-color: lighten($color-alpha, 50%);
    }
  }
}

code {
  font-size: ms(-1);
  display: inline-block;
  background-color: lighten($color-alpha, 50%);
  border-color: lighten($color-alpha, 40%);
  border-style: solid;
  border-width: 1px 3px;
  border-radius: .25rem;
  padding-right: .125rem;
  padding-left: .125rem;

  &::selection {
    background-color: lighten($color-alpha, 10%);
    color: $color-psi;
    text-shadow: 0 1px 0 $color-omega;
  }
}

.article__title--alpha {
  font-size: ms(1);
  text-shadow: 0 1px 0 $color-omega;

  a {
    color: $color-beta;
    display: block;
  }
}

.article__title--beta {
  grid-area: title;
  margin-bottom: 0;

  @include mq($until: tablet) {
    font-size: ms(1);
  }

  @include mq($from: tablet) {
    margin-right: calc((100vw - 100%) / -2);
    margin-left: calc((100vw - 100%) / -2);
    padding-left: 1em;
    padding-right: 1em;
    text-align: center;
  }
}

.article__title--gamma {
  font-size: ms(2);
  margin-bottom: 0;
  grid-area: title;

  @include mq($from: tablet) {
    font-size: ms(4);
  }
}

.article__testimonials {
  position: relative;

  &:after {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 15%;
    max-width: 100px;
    height: 10px;
    background-color: lighten($color-alpha, 20%);
  }
}

.article__thumb {
  margin-right: calc((100vw - 100%) / -2);
  margin-left: calc((100vw - 100%) / -2);
  display: block;

  img {
    display: block;
  }

  @include mq($from: mobile) {
    margin-right: 0;
    margin-left: 0;
  }
}

.details {
  margin-top: 0;
  margin-bottom: 0;
  padding: 1em 0;

  .main--post & {
    margin-bottom: 2em;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: calc((100vw - 100%) / -2);
      bottom: 0;
      left: calc((100vw - 100%) / -2);
      background-color: $color-gamma;
      z-index: -1;
    }

    @include mq($from: mobile) {
      margin-top: 1em;
      max-width: 720px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.project {
  font-size: ms(-1);
  margin-top: rem(15px);
  margin-bottom: 0;

  a {
    text-transform: uppercase;
    font-weight: bold;
    @include ls(40);
    margin-right: rem(3px);
  }

  @include mq($until: tablet) {
    text-align: center;
  }
}

.metadata {
  grid-area: meta;
  margin: 2em 0;
  display: grid;
  grid-template-columns: 1fr;

  h3,
  p,
  li {
    line-height: 30px;
  }

  h3,
  p {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }

  h3 {
    color: $color-psi;
    font-size: ms(0);
    letter-spacing: calc(0.15em / 4);
  }

  li {
    margin-bottom: 0;
  }

  li,
  p {
    font-size: 0.825rem;
  }

  @include mq($from: tablet) {
    grid-template-columns: 1fr 1fr;
    grid-gap: 25px;
  }
}

.content {
  h2,
  h3,
  h4 {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      top: 100%;
      left: 0;
      width: 15%;
      max-width: 100px;
      height: 10px;
      background-color: lighten($color-alpha, 20%);
    }
  }

  > *:first-child {
    margin-top: 0;
  }

  .main--post & {
    max-width: 720px;
    margin: 0 auto;
  }

  p {
    img {
      width: auto;
    }
  }

  a {
    .main--sidebar & {
      word-break: break-all;
      hyphens: auto;
    }
  }
}

.comments {
  .main--post & {
    max-width: 720px;
    margin: 0 auto;
  }
}
